/* src/styles/App.css */
@import url('https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&family=Jua&display=swap');

:root {
  --vh: 1vh;
}

body, html, #root {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  overflow: hidden;
  font-family: 'Jua', sans-serif;
  background-color: #f0f4f8;
  color: #333;
}

.app-container {
  display: flex;
  width: 100%;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  transition: all 0.3s ease;
  overflow: hidden;
}

/* 사이드바가 열렸을 때 메인 컨텐츠를 오른쪽으로 밀어내는 역할 */
.app-container.sidebar-open .main-content {
  margin-left: 250px;
}

/* 퀴즈가 활성화되면 사이드바 여백을 제거하고 메인 컨텐츠가 전체 너비를 차지하도록 함 */
.app-container.quiz-active .main-content {
  margin-left: 0;
}