/* src/styles/Sidebar.css */
.sidebar {
  position: fixed;
  top: 0;
  left: -250px;
  width: 250px;
  height: 100vh;
  background-color: #f4f4f4;
  padding: 20px;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  transition: left 0.3s ease;
  z-index: 100;
  display: flex;
  flex-direction: column;
}

.sidebar.open {
  left: 0;
}

/* 퀴즈 진행 중에는 사이드바가 완전히 숨겨짐 */
.sidebar.quiz-active {
  left: -250px;
}

.sidebar-toggle-button {
  position: fixed;
  top: 20px; /* 상단에 위치 */
  left: 20px; /* 좌측에 위치 */
  width: 45px;
  height: 40px;
  background-color: lightgreen;
  color: yellow;
  border: 1px solid #ccc;
  border-radius: 8px; /* 사각형 모서리 */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.9rem;
  font-weight: bold;
  cursor: pointer;
  z-index: 101;
  transition: all 0.3s ease;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
}

.sidebar-toggle-button.open {
  transform: translateX(250px);
}

.sidebar-header {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #ddd;
}

.sidebar-user-name {
  font-size: 1.2rem;
  font-weight: bold;
}

.sidebar-history-button {
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  cursor: pointer;
  font-size: 0.9em;
  transition: background-color 0.2s;
}

.sidebar-history-button:hover {
  background-color: #2980b9;
}

.menu-section {
  margin-bottom: 20px;
}

.menu-section h2 {
  font-size: 1rem;
  margin-bottom: 10px;
}

.options-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
}

.sidebar-button {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 10px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background-color 0.2s;
  text-align: center;
}

.sidebar-button.active {
  background-color: #007bff;
  color: white;
  border-color: #007bff;
}

.sidebar-button:hover:not(.active) {
  background-color: #f0f0f0;
}

.sidebar-button:disabled {
  background-color: #e9ecef;
  color: #6c757d;
  cursor: not-allowed;
}

.start-button {
  padding: 15px;
  font-size: 1.2rem;
  font-weight: bold;
  color: white;
  background-color: #28a745;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: auto;
  transition: background-color 0.2s;
}

.start-button:hover:not(:disabled) {
  background-color: #218838;
}

.start-button:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
}