/* src/styles/MainContent.css */
.main-content {
  flex: 1;
  padding: 20px;
  transition: margin-left 0.3s ease;
}

.main-content-welcome {
  text-align: center;
  padding-top: 100px;
}

.main-content-welcome h2 {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 20px;
}

.main-content-welcome p {
  font-size: 1.2rem;
  color: #666;
}

.user-header {
  position: absolute;
  top: 20px;
  right: 20px;
  display: flex; /* 가로 정렬 */
  align-items: center; /* 세로 중앙 정렬 */
  gap: 15px;
  padding: 10px;
  border-radius: 8px;
}

.user-name {
  font-weight: bold;
  font-size: 1.1em;
  color: #34495e;
}

.logout-button {
  width: 30px;
  height: 30px;
  padding: 0;
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s;
}

.logout-button:hover {
  background-color: #c82333;
}

.sound-toggle-button {
  position: absolute;
  top: 20px;
  left: 20px;
  padding: 5px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  cursor: pointer;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: background-color 0.2s ease;
}

.sound-toggle-button:hover {
  background-color: #f0f0f0;
}