/* src/styles/HistoryPopup.css */

.history-popup {
  max-width: 90vw;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  padding: 20px;
}

.history-list-container {
  max-height: calc(90vh - 150px);
  overflow-y: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 10px;
}

.history-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.history-item {
  border-bottom: 1px solid #eee;
  padding: 10px 0;
  cursor: pointer;
  user-select: none;
}

.history-item:last-child {
  border-bottom: none;
}

.history-summary {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  color: #555;
}

.history-summary:hover {
  color: #000;
}

.expand-icon {
  font-size: 1.2em;
  margin-left: 10px;
}

.history-details {
  margin-top: 10px;
  background-color: #f9f9f9;
  border-radius: 5px;
  padding: 10px;
}

.quiz-settings {
  font-size: 0.9em;
  color: #777;
  margin-bottom: 10px;
}

.problem-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.problem-item {
  padding: 5px 0;
}

.problem-question {
  color: #333;
}

.problem-answer {
  font-weight: bold;
  margin-left: 10px;
}

.problem-answer.correct {
  color: #28a745;
}

.problem-answer.incorrect {
  color: #dc3545;
}

.no-history-message {
  text-align: center;
  color: #888;
  padding: 20px;
}

.popup-close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 1.5em;
  cursor: pointer;
}

.popup-confirm-button {
  width: 100%;
  padding: 12px;
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
  font-size: 1em;
}